import { Tooltip } from 'antd'
import React from 'react'
import { Link } from 'react-router-dom'

function Header() {
    return (
        <div>
            <Link to="/">
                <Tooltip title="홈으로 이동합니다.">
                    <img alt="logo" src="images/logo.png" style={{width: "100%", maxWidth: "600px"}}/>
                </Tooltip>
            </Link>
        </div>
    )
}

export default Header

